import "../../../../chunks/client.js";
import "just-clone";
import "ts-deepmerge";
import "../../../../chunks/formData.js";
import { f as fail, r as redirect } from "../../../../chunks/index.js";
import { s as superValidate } from "../../../../chunks/superValidate.js";
import { a as signUpFormSchema } from "../../../../chunks/auth.js";
import "memoize-weak";
import { a as zod } from "../../../../chunks/zod.js";
import { p as pb } from "../../../../chunks/pb.js";
const load = async () => {
  return {
    form: await superValidate(zod(signUpFormSchema))
  };
};
const actions = {
  default: async (event) => {
    const form = await superValidate(event, zod(signUpFormSchema));
    try {
      await pb.collection("users").getFirstListItem(`username="${form.data.username}" || email="${form.data.email}"`);
      return fail(409, {
        message: {
          text: "User sudah ada",
          variant: "destructive"
        },
        form
      });
    } catch (e) {
      await pb.collection("users").create({
        username: form.data.username,
        email: form.data.email,
        password: form.data.password,
        passwordConfirm: form.data.confirmPassword,
        emailVisibility: true,
        name: form.data.username,
        bio: "Senang bertemu dengan anda",
        role: "student"
      });
    }
    await pb.collection("users").authWithPassword(form.data.email, form.data.password);
    redirect(302, "/dashboard");
  }
};
export {
  actions,
  load
};
